.details{
  &__wrapper{
    display: grid;
    row-gap: 40px;

    @include onTablet{
      row-gap: 48px;
    }
  }

  .detail{
    &__photos{
    @include pageGrid;
    margin-bottom: 16px;
    }
    &__link{
      position: relative;

      &-wide{
        @include onTablet{
        grid-column: span 4;
        }
        @include onDesktop{
          grid-column: span 8;
        }
    }
    &-square{
      padding-bottom: 100%;
      height: 0;
      @include onTablet{
        grid-column: span 2;
      }
      @include onDesktop{
        grid-column: span 4;
      }
  }
    }
    }
&__photo{
  position:absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

    &__title{
      margin: 0;
      font-weight: 700;
      font-size: 24px;
      line-height: 33px;
    }

    p{
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    }
}



